module.exports = "/* Include the padding and border in an element's total width and height */\n*\n.checked {\n  background: #888;\n  color: #fff;\n  text-decoration: line-through;\n  font-size: 20px;\n  font-weight: bold; }\n.unchecked {\n  background: yellow;\n  color: black;\n  text-decoration: none;\n  font-size: 20px;\n  font-weight: bold; }\n/* Style the header */\n.header {\n  background-color: #f44336;\n  padding: 30px 40px;\n  color: white;\n  text-align: center; }\n/* Clear floats after the header */\n.header:after {\n  content: \"\";\n  display: table;\n  clear: both; }\n/* Style the input */\ninput {\n  margin: 0;\n  border: none;\n  border-radius: 0;\n  width: 75%;\n  padding: 10px;\n  float: left;\n  font-size: 16px; }\n/* Style the \"Add\" button */\n.addBtn {\n  padding: 10px;\n  width: 25%;\n  background: #d9d9d9;\n  color: #555;\n  float: left;\n  text-align: center;\n  font-size: 16px;\n  cursor: pointer;\n  transition: 0.3s;\n  border-radius: 0; }\n.addBtn:hover {\n  background-color: #bbb; }\n"